.grafica-dia{
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 3;
    grid-row-end: 4;
    }

.grafica-dia-titulo{
    text-align: center;
}


.grafica-dia-container{
    max-width: 94%;
    font-size: 12px;
}

.grafica-dia-botones{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.grafica-dia-boton{
    padding: 5px 20px;
    margin: 10px 5px;
    border-radius: 5px;
    font-size: 17px;
    cursor: pointer;
}


